.heart-box {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 15px;
  padding: 0%;
  display: flex;
}

.heart {
  cursor: pointer;
  background: red;
  width: 15px;
  height: 15px;
  animation: .6s linear infinite heart;
  position: relative;
  transform: rotate(-45deg);
  box-shadow: -1px 1px 9px red;
}

.heart:before {
  content: "";
  background: red;
  border-radius: 50px;
  width: 15px;
  height: 15px;
  position: absolute;
  top: -50%;
  box-shadow: -1px -1px 9px red;
}

.heart:after {
  content: "";
  background: red;
  border-radius: 50px;
  width: 15px;
  height: 15px;
  position: absolute;
  right: -50%;
  box-shadow: 1px 1px 9px red;
}

@keyframes heart {
  0% {
    transform: rotate(-45deg)scale(1.07);
  }

  80% {
    transform: rotate(-45deg)scale(1);
  }

  100% {
    transform: rotate(-45deg)scale(.8);
  }
}
