body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-clash-display: "Clash Display";
  --font-nunito-sans: "Nunito Sans";
  --font-dm-sans: "DM Sans";
  --font-plus-jakarta-sans: "Plus Jakarta Sans";

  /* font sizes */
  --font-size-base: 16px;
  --font-size-13xl: 32px;
  --font-size-lgi: 19px;
  --font-size-7xl: 26px;
  --font-size-8xs-2: 4.2px;
  --font-size-xs-8: 11.8px;
  --font-size-lg: 18px;
  --font-size-29xl: 48px;
  --font-size-10xl: 29px;
  --font-size-19xl: 38px;
  --font-size-9xl: 28px;
  --font-size-3xl: 22px;
  --font-size-xl: 20px;

  /* Colors */
  --color-white: #fff;
  --color-gray-100: rgba(0, 0, 0, 0.5);
  --color-black: #000;
  --color-darkslategray: #333;
  --color-tomato: #ff5555;

  /* Gaps */
  --gap-73xl: 92px;
  --gap-4xl: 23px;
  --gap-27xl: 46px;
  --gap-xl: 20px;
  --gap-base: 16px;
  --gap-16xl: 35px;
  --gap-xs: 12px;
  --gap-5xs: 8px;
  --gap-2xl: 21px;
  --gap-11xl: 30px;
  --gap-40xl: 59px;
  --gap-5xl: 24px;
  --gap-9xs: 4px;
  --gap-14xl: 33px;

  /* Paddings */
  --padding-21xl: 40px;
  --padding-xl: 20px;
  --padding-17xl: 36px;
  --padding-57xl: 76px;
  --padding-19xl: 38px;
  --padding-20xl: 39px;
  --padding-3xl: 22px;
  --padding-11xs: 2px;
  --padding-4xl: 23px;
  --padding-4xs: 9px;
  --padding-6xs: 7px;
  --padding-xs: 12px;
  --padding-sm: 14px;
  --padding-mini-4: 14.4px;
  --padding-5xl: 24px;
  --padding-18xl: 37px;
  --padding-12xl: 31px;
  --padding-11xl: 30px;
  --padding-10xl: 29px;
  --padding-8xl: 27px;
  --padding-15xl: 34px;
  --padding-base: 16px;
  --padding-smi: 13px;
  --padding-12xs: 1px;
  --padding-9xl: 28px;
  --padding-2xs: 11px;

  /* Border radiuses */
  --br-9xs: 4px;
  --br-5xs: 8px;
  --br-base: 16px;

  /* Effects */
  --s: -13px 13px 104.09px rgba(161, 161, 161, 0.1);
}

