.heart-box{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  gap: 15px;
  padding: 0%;
}
.heart{
  
  cursor: pointer;
  background: red;
  width:15px;
  height: 15px;
  position: relative;
  transform: rotate(-45deg);
  box-shadow: -1px 1px 9px red;
  animation: heart 0.6s linear infinite;
}

.heart:before{
  content: '';
  position: absolute;
  height: 15px;
  width: 15px;
  background: red;
  top: -50%;
  box-shadow: -1px -1px 9px red;
  border-radius: 50px;
}

.heart:after{
  content: '';
  position: absolute;
  height: 15px;
  width: 15px;
  background: red;
  right: -50%;
  border-radius: 50px;
  box-shadow: 1px 1px 9px red;
}

@keyframes heart{
  0%{
      transform: rotate(-45deg) scale(1.07);
  }

  80%{
      transform: rotate(-45deg) scale(1.0); 
  }

  100%{
      transform: rotate(-45deg) scale(0.8);
  }
}